.container {
    display: flex;
    flex-direction: column;
}
.titleBlock{
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
}
.taskBlock{
    display: flex;
    flex-direction: column;
}
.task{
    display: flex;
    justify-content: space-between;
}